.App {
  min-height: 100vh;
  background: var(--bg-primary);
  color: var(--text-primary);
  transition: background-color 0.5s ease, color 0.5s ease;
}

.main-content {
  position: relative;
  width: 100%;
  overflow-x: hidden;
}

/* Section Styling */
.section {
  position: relative;
  padding: 8rem 2rem;
  background: var(--bg-primary);
  overflow: hidden;
}

.section-container {
  max-width: 1400px;
  margin: 0 auto;
  position: relative;
  z-index: 10;
}

/* Section Header */
.section-header {
  margin-bottom: 5rem;
}

.section-header.centered {
  text-align: center;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 5rem;
}

.section-eyebrow {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.section-header.centered .section-eyebrow {
  justify-content: center;
}

.eyebrow-line {
  width: 60px;
  height: 1px;
  background: var(--text-tertiary);
}

.section-title {
  font-size: clamp(2.5rem, 5vw, 4rem);
  font-weight: 800;
  margin-bottom: 1.5rem;
  color: var(--text-primary);
  font-family: 'Space Grotesk', sans-serif;
  letter-spacing: -0.02em;
  line-height: 1.2;
}

.section-description {
  font-size: 1.125rem;
  line-height: 1.7;
  color: var(--text-secondary);
  max-width: 700px;
}

.section-header.centered .section-description {
  margin: 0 auto;
}

/* About Section */
.about-section {
  background: var(--bg-secondary);
}

.about-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2.5rem;
  margin-top: 4rem;
}

.about-card {
  position: relative;
  padding: 3rem 2rem;
  background: var(--bg-primary);
  border: 1px solid var(--border);
  border-radius: 8px;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  text-align: center;
}

.about-card::before {
  content: '';
  position: absolute;
  inset: 0;
  background: linear-gradient(135deg, var(--overlay) 0%, transparent 100%);
  opacity: 0;
  transition: opacity 0.4s ease;
  border-radius: 8px;
  pointer-events: none;
}

.about-card:hover {
  transform: translateY(-10px);
  border-color: var(--text-primary);
  box-shadow: 0 20px 60px var(--shadow-hover);
}

.about-card:hover::before {
  opacity: 1;
}

.about-icon {
  font-size: 3rem;
  margin-bottom: 1.5rem;
  filter: drop-shadow(0 4px 12px var(--shadow));
}

.about-card h3 {
  font-size: 1.75rem;
  font-weight: 700;
  margin-bottom: 1rem;
  color: var(--text-primary);
  font-family: 'Space Grotesk', sans-serif;
}

.about-card p {
  font-size: 1rem;
  line-height: 1.6;
  color: var(--text-secondary);
}

/* Process Section */
.process-section {
  background: var(--bg-primary);
  position: relative;
}

.process-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(
    90deg,
    transparent 0%,
    var(--border) 50%,
    transparent 100%
  );
}

.process-timeline {
  max-width: 900px;
  margin: 0 auto;
  position: relative;
}

.process-step {
  position: relative;
  display: flex;
  align-items: flex-start;
  gap: 2rem;
  padding: 2.5rem 0;
  transition: all 0.3s ease;
}

.process-step:hover {
  transform: translateX(10px);
}

.step-number {
  flex-shrink: 0;
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.75rem;
  font-weight: 900;
  font-family: 'Space Grotesk', sans-serif;
  color: var(--text-primary);
  border: 2px solid var(--border);
  border-radius: 50%;
  background: var(--bg-secondary);
  transition: all 0.4s ease;
  position: relative;
  z-index: 2;
}

.process-step:hover .step-number {
  border-color: var(--text-primary);
  background: var(--text-primary);
  color: var(--bg-primary);
  transform: rotate(360deg) scale(1.1);
}

.step-content {
  flex: 1;
  padding-top: 0.5rem;
}

.step-content h3 {
  font-size: 1.75rem;
  font-weight: 700;
  margin-bottom: 0.75rem;
  color: var(--text-primary);
  font-family: 'Space Grotesk', sans-serif;
}

.step-content p {
  font-size: 1rem;
  line-height: 1.6;
  color: var(--text-secondary);
}

.step-connector {
  position: absolute;
  left: 39px;
  top: 80px;
  width: 2px;
  height: calc(100% - 40px);
  background: linear-gradient(
    180deg,
    var(--border) 0%,
    var(--border) 50%,
    transparent 100%
  );
  z-index: 1;
}

.process-step:last-child .step-connector {
  display: none;
}

/* Testimonials Section */
.testimonials-section {
  background: var(--bg-secondary);
}

.testimonials-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 2.5rem;
  margin-top: 4rem;
}

.testimonial-card {
  position: relative;
  padding: 3rem 2.5rem;
  background: var(--bg-primary);
  border: 1px solid var(--border);
  border-radius: 8px;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

.testimonial-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 0;
  background: var(--text-primary);
  transition: height 0.4s ease;
  border-radius: 8px 0 0 8px;
}

.testimonial-card:hover {
  transform: translateY(-10px);
  border-color: var(--text-primary);
  box-shadow: 0 20px 60px var(--shadow-hover);
}

.testimonial-card:hover::before {
  height: 100%;
}

.testimonial-quote {
  font-size: 5rem;
  font-weight: 900;
  font-family: 'Space Grotesk', sans-serif;
  color: var(--text-tertiary);
  opacity: 0.2;
  line-height: 1;
  margin-bottom: 1rem;
}

.testimonial-text {
  font-size: 1.125rem;
  line-height: 1.7;
  color: var(--text-secondary);
  margin-bottom: 2rem;
  font-style: italic;
}

.testimonial-author {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.author-avatar {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: linear-gradient(135deg, var(--text-primary), var(--text-tertiary));
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-family: 'Space Grotesk', sans-serif;
  color: var(--bg-primary);
  font-size: 1.25rem;
  flex-shrink: 0;
}

.author-info h4 {
  font-size: 1.125rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 0.25rem;
}

.author-info p {
  font-size: 0.875rem;
  color: var(--text-tertiary);
}

/* Contact Section */
.contact-section {
  background: var(--bg-primary);
  padding: 8rem 2rem;
}

.contact-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 5rem;
  align-items: start;
}

.contact-content {
  position: sticky;
  top: 120px;
}

.contact-info {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  margin-top: 3rem;
}

.contact-item {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  padding: 1.5rem;
  background: var(--bg-secondary);
  border: 1px solid var(--border);
  border-radius: 8px;
  transition: all 0.3s ease;
}

.contact-item:hover {
  border-color: var(--text-primary);
  transform: translateX(10px);
}

.contact-icon {
  font-size: 2rem;
  filter: drop-shadow(0 2px 8px var(--shadow));
}

.contact-details h4 {
  font-size: 1rem;
  font-weight: 600;
  color: var(--text-tertiary);
  margin-bottom: 0.25rem;
  text-transform: uppercase;
  letter-spacing: 0.1em;
}

.contact-details p {
  font-size: 1.25rem;
  color: var(--text-primary);
  font-weight: 500;
}

/* Contact Form */
.contact-form-wrapper {
  background: var(--bg-secondary);
  border: 1px solid var(--border);
  border-radius: 8px;
  padding: 3rem;
  position: relative;
  overflow: hidden;
}

.contact-form-wrapper::before {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  height: 4px;
  background: linear-gradient(
    90deg,
    var(--text-primary) 0%,
    var(--text-tertiary) 100%
  );
  border-radius: 8px 8px 0 0;
}

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-group label {
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.form-group input,
.form-group select,
.form-group textarea {
  padding: 1rem 1.25rem;
  background: var(--bg-primary);
  border: 1px solid var(--border);
  border-radius: 4px;
  color: var(--text-primary);
  font-size: 1rem;
  font-family: 'Inter', sans-serif;
  transition: all 0.3s ease;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--text-primary);
  box-shadow: 0 0 0 3px var(--overlay);
}

.form-group textarea {
  resize: vertical;
  min-height: 120px;
}

.submit-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  padding: 1.25rem 2.5rem;
  background: var(--text-primary);
  color: var(--bg-primary);
  border: 2px solid var(--text-primary);
  border-radius: 4px;
  font-size: 1rem;
  font-weight: 600;
  font-family: 'Space Grotesk', sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

.submit-button::before {
  content: '';
  position: absolute;
  inset: 0;
  background: var(--bg-primary);
  transform: translateX(-100%);
  transition: transform 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: -1;
}

.submit-button:hover::before {
  transform: translateX(0);
}

.submit-button:hover {
  color: var(--text-primary);
}

.submit-button:active {
  transform: scale(0.98);
}

.button-arrow {
  font-size: 1.5rem;
  transition: transform 0.3s ease;
}

.submit-button:hover .button-arrow {
  transform: translateX(5px);
}

/* Footer */
.footer {
  background: var(--bg-secondary);
  border-top: 1px solid var(--border);
  padding: 5rem 2rem 2rem;
}

.footer-container {
  max-width: 1400px;
  margin: 0 auto;
}

.footer-grid {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr;
  gap: 3rem;
  margin-bottom: 3rem;
}

.footer-column h3,
.footer-column h4 {
  font-family: 'Space Grotesk', sans-serif;
  font-weight: 700;
  margin-bottom: 1.5rem;
  color: var(--text-primary);
}

.footer-logo h3 {
  font-size: 1.75rem;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

.footer-logo p {
  font-size: 0.875rem;
  color: var(--text-tertiary);
  font-style: italic;
  margin-top: 0.5rem;
}

.footer-description {
  font-size: 0.95rem;
  line-height: 1.6;
  color: var(--text-secondary);
  margin-top: 1rem;
}

.footer-column ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.footer-column ul li a {
  color: var(--text-secondary);
  text-decoration: none;
  font-size: 0.95rem;
  transition: all 0.3s ease;
  display: inline-block;
}

.footer-column ul li a:hover {
  color: var(--text-primary);
  transform: translateX(5px);
}

.footer-bottom {
  padding-top: 2rem;
}

.footer-line {
  width: 100%;
  height: 1px;
  background: linear-gradient(
    90deg,
    transparent 0%,
    var(--border) 50%,
    transparent 100%
  );
  margin-bottom: 2rem;
}

.footer-bottom-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 1rem;
  font-size: 0.875rem;
  color: var(--text-tertiary);
}

.footer-links {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.footer-links a {
  color: var(--text-tertiary);
  text-decoration: none;
  transition: color 0.3s ease;
}

.footer-links a:hover {
  color: var(--text-primary);
}

/* Responsive Design */
@media (max-width: 1024px) {
  .section {
    padding: 6rem 2rem;
  }

  .contact-wrapper {
    grid-template-columns: 1fr;
    gap: 3rem;
  }

  .contact-content {
    position: static;
  }

  .footer-grid {
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
  }

  .footer-column:first-child {
    grid-column: 1 / -1;
  }
}

@media (max-width: 768px) {
  .section {
    padding: 5rem 1.5rem;
  }

  .section-title {
    font-size: clamp(2rem, 4vw, 3rem);
  }

  .section-description {
    font-size: 1rem;
  }

  .about-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .process-step {
    flex-direction: column;
    gap: 1.5rem;
  }

  .step-connector {
    display: none;
  }

  .testimonials-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .contact-form-wrapper {
    padding: 2rem;
  }

  .footer-grid {
    grid-template-columns: 1fr;
  }

  .footer-column:first-child {
    grid-column: 1;
  }

  .footer-bottom-content {
    flex-direction: column;
    text-align: center;
  }
}

@media (max-width: 480px) {
  .section {
    padding: 4rem 1rem;
  }

  .about-card,
  .testimonial-card {
    padding: 2rem 1.5rem;
  }

  .step-number {
    width: 60px;
    height: 60px;
    font-size: 1.25rem;
  }

  .step-content h3 {
    font-size: 1.5rem;
  }

  .testimonial-quote {
    font-size: 4rem;
  }

  .contact-form-wrapper {
    padding: 1.5rem;
  }

  .submit-button {
    width: 100%;
  }
}

/* Accessibility */
.form-group input:focus-visible,
.form-group select:focus-visible,
.form-group textarea:focus-visible {
  outline: 2px solid var(--text-primary);
  outline-offset: 2px;
}

.submit-button:focus-visible {
  outline: 2px solid var(--text-primary);
  outline-offset: 4px;
}

/* Performance */
.about-card,
.process-step,
.testimonial-card,
.contact-item {
  will-change: transform;
}
